.say {
  padding-bottom: 100px;
}

.say__top {
  max-width: 543px;
}

.say__group__info {
  justify-content: space-between;
  align-items: center;
  padding: 0 90px;
  display: flex;
}

.say__group__info__block {
  border-radius: 6px;
  flex: 0 32%;
  margin-right: 0;
  padding: 30px 24px 38px 15px;
  transition: box-shadow .2s;
  box-shadow: 0 13px 45px #0000001a;
}

.say__group__info__block:hover {
  box-shadow: 0 5px 20px #0003;
}

.say__group__info__block__text {
  color: #555;
  margin-bottom: 20px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
}

.say__group__info__block__who {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.say__group__info__block__who__image {
  margin-right: 16px;
}

.say__group__info__block__who__image img {
  max-width: 100%;
}

.say__group__info__block__who__person__name {
  color: #222;
  margin-bottom: 8px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 26px;
}

.say__group__info__block__who__person__post {
  color: #555;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
}

.say__group {
  position: relative;
}

.say__group__reviews__btn {
  z-index: 1;
  color: #0000;
  border-radius: 50%;
  font-size: 0;
  position: absolute;
  top: 30%;
  transform: translateY(-50%);
}

.arrow2 {
  width: 45px;
  height: 45px;
  cursor: pointer;
  border: 1px solid #d5d4d4;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.arrow3 {
  width: 45px;
  height: 45px;
  cursor: pointer;
  border: 1px solid #086cf9;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.arrow--active {
  background-color: #086cf9;
  transition: background .1s linear;
}

.arrow--active:hover {
  background-color: #0047ab;
}

.say__group__reviews__btn:after {
  content: "";
  width: 16px;
  height: 16px;
  border-top: 3px solid #d5d4d4;
  border-left: 3px solid #d5d4d4;
  display: block;
}

.btn--prev {
  left: 16px;
  transform: rotate(-45deg);
}

.btn--next {
  right: 16px;
  transform: rotate(135deg);
}

@media (max-width: 1000px) {
  .say__group__info {
    padding: 0 60px;
  }
}

@media (max-width: 830px) {
  .say__group__info__block {
    flex: 0 31%;
    padding: 25px 20px 34px 13px;
  }
}

@media (max-width: 785px) {
  .say__group__info__block {
    flex: 0 48%;
    padding: 30px 24px 38px 15px;
  }

  .none4 {
    display: none;
  }
}

@media (max-width: 570px) {
  .say__group__info__block {
    flex: 0 48%;
    padding: 25px 15px 34px 10px;
  }

  .say {
    padding-bottom: 80px;
  }
}

@media (max-width: 513px) {
  .say__group__info {
    justify-content: center;
  }

  .say__group__info__block {
    flex: 0 95%;
    padding: 30px 24px 38px 15px;
  }

  .none5 {
    display: none;
  }

  .arrow2, .arrow3 {
    width: 35px;
    height: 35px;
  }

  .btn--prev {
    left: 12px;
  }

  .btn--next {
    right: 12px;
  }
}

@media (max-width: 390px) {
  .say__group__info__block {
    flex: 0 100%;
    padding: 15px 15px 20px 10px;
  }

  .say__group__info {
    padding: 0 52px;
  }
}

.subscribe {
  max-width: 939px;
  height: 325px;
  text-align: center;
  background: #086cf9;
  border-radius: 6px;
  margin: 110px auto 0;
  padding: 48px 25px 64px;
}

.top--white1 {
  color: #fcfcfc;
}

.top--white2 {
  color: #f9f9f9;
}

.subscribe__form {
  max-width: 538px;
  height: 64px;
  background: #fff;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  margin: 40px auto 0;
  transition: border .2s linear;
  display: flex;
}

.subscribe__form:hover {
  border: 1px solid #0047ab;
}

.subscribe__form__input {
  padding: 20px 24px;
  font-family: inherit;
  font-size: 14px;
}

.subscribe__form__button {
  margin-right: 8px;
}

.subscribe__form__button:focus {
  background: #0047ab;
}

@media (max-width: 500px) {
  .subscribe {
    margin: 80px auto 0;
  }
}

@media (max-width: 362px) {
  .subscribe__form {
    height: 54px;
  }

  .subscribe {
    height: 300px;
    margin: 60px auto 0;
    padding: 48px 15px 64px;
  }

  .subscribe__form__button {
    margin-right: 8px;
    padding: 8px 12px;
  }

  .subscribe__form__input {
    padding: 5px 0 5px 24px;
  }
}

